/*------------ GALERIA DE IMAGEN---------- */
 /* ###################################### */
.carousel-wrapper {
  /* z-index: -3; */
  /* flex: 100%; */
  height: 400px;
  position: relative;
  width: 700px;
  display: block;
  margin: 50px auto ;
  border: 2px solid black;
  
  z-index: 0;
}

.carousel-item {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 25px 50px;
  opacity: 0;
  transition: all 0.5s ease-in-out;
}
.arrow{
  border: solid #b0b0b0;;
  border-width: 0 5px 5px 0;
  display: inline-block;
  padding: 12px;
}
.arrow-prev {
  left: 30px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(135deg);

}
.arrow-next {
  right: 30px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(-45deg);
}

[id^= "item"] {
  display: none;
}
[id^= "item"] {
  display: none;
}
.item-1 {
  z-index: 2;
  opacity: 1;
  background: url('../img/img1.jpg');
  background-size: cover;
}
.item-2 {
  background:url('../img/img2.jpeg');
  background-size: cover;
}
.item-3 {
  background:url('../img/img3.jpg');
  background-size: cover;
}

*:target ~ .item-1{
  opacity: 0;
}
#item-1:target ~ .item-1 {
  opacity: 1;

}
#item-2:target ~ .item-2, #item-3:target ~ .item-3 {
  z-index: 3;
  opacity: 1;
} 


@media(max-width: 780px){
